<template>
	<div id="mainHeader">
		<div class="title">
			<h1  :class="addMobileCls?'text-white':null">RYAN LI</h1>
			<p :class="addMobileCls?'text-white':null" v-if="$route.path!=='/aboutMe'" class="pointer subTitle" @click="handleTo">About me</p>
			<p :class="addMobileCls?'text-white':null" v-if="$route.path!=='/main'" class="pointer subTitle" @click="$router.push('./main')">Artical & Repos</p>
		</div>
	</div>
</template>


<script>
	import { mapGetters } from "vuex";

	export default {
		name: "mainHeader",
		computed: {
			path() {
				return this.$route.path;
			}
		},
		computed: {
			...mapGetters(["screenSize"]),
			addMobileCls() {
				return this.screenSize < 575 ? true : false;
			}
		},
		methods: {
			handleTo() {
				this.$router.push("./aboutMe");
			}
		}
	};
</script>



